@import url("https://fonts.googleapis.com/css2?family=Inter:wght@100..900&display=swap");
* {
  padding: 0;
  margin: 0;
  border: 0;
}

*,
*:before,
*:after {
  -moz-box-sizing: border-box;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
}

:focus,
:active {
  outline: none;
}

a:focus,
a:active {
  outline: none;
}

nav,
footer,
header,
aside {
  display: block;
}

html,
body {
  height: 100%;
  width: 100%;
  font-size: 100%;
  line-height: 1;
  font-size: 14px;
  -ms-text-size-adjust: 100%;
  -moz-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
}

input,
button,
textarea {
  font-family: inherit;
}

input::-ms-clear {
  display: none;
}

button {
  cursor: pointer;
}

button::-moz-focus-inner {
  padding: 0;
  border: 0;
}

a,
a:visited {
  text-decoration: none;
}

a:hover {
  text-decoration: none;
}

ul li {
  list-style: none;
}

img {
  vertical-align: top;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-size: inherit;
  font-weight: 400;
}

html {
  scroll-behavior: smooth;
}

body {
  background: #F8F8F8;
}

* {
  line-height: 130%;
}

.login {
  padding: 0 20px;
  width: 100vw;
  height: 100vh;
  background: url("../img/login__bg.png");
  background-size: cover;
  display: flex;
  flex-wrap: nowrap;
  justify-content: center;
  align-items: center;
  flex-direction: row;
  flex-direction: column;
}
.login__top {
  text-align: center;
  margin-bottom: 50px;
}
.login__logo {
  width: 44px;
  height: 44px;
  border: 1px solid rgba(36, 34, 32, 0.8509803922);
  border-radius: 50%;
  margin: 0 auto 24px auto;
  display: flex;
  flex-wrap: nowrap;
  justify-content: center;
  align-items: center;
  flex-direction: row;
}
.login__title {
  font-family: "Inter", sans-serif;
  font-size: 30px;
  color: #242220;
  margin-bottom: 12px;
}
.login__text {
  font-family: "Inter", sans-serif;
  font-size: 16px;
  color: #242220;
}
.login__content {
  display: flex;
  flex-direction: column;
  gap: 20px;
  max-width: 460px;
  width: 100%;
}
.login__button {
  font-family: "Inter", sans-serif;
  display: flex;
  flex-wrap: nowrap;
  justify-content: center;
  align-items: center;
  flex-direction: row;
  gap: 15px;
  background: #FFFFFF;
  display: flex;
  width: 100%;
  padding: 20px 0;
  border-radius: 5px;
  font-size: 20px;
  color: rgba(36, 34, 32, 0.8509803922);
}
.login__privacy {
  font-family: "Inter", sans-serif;
  position: absolute;
  bottom: 15px;
  right: 10px;
  font-size: 12px;
  color: #000;
}
@media screen and (max-width: 450px) {
  .login__button {
    font-size: 14px;
  }
}